
export class StorageService {

    checklists() {
        return [
        {
            name: "Poranek",
            id: "poranek",
            items: ["Łóżko pościelić","Ogolić się","Umyć zęby","Umyć twarz","Ubrać \"wyjściową\" koszulkę"]
        },
        {
            name: "Bad day",
            id: "badDay",
            items: ["Czy mnie coś boli?","Gonitwa myśli? -> journaling","Dość światła?","Jestem głodny? Albo czy coś w ogóle jadłem?","Chce mi się pić?","Czy jestem zmęczony?","Czy odpowiednio długo spałem?","Czy to kolejny dzień z małą ilością snu?"]
        },
        {
            name: "Koniec pracy",
            id: "endOfDay",
            items: ["Update ToDo - co zrobiłem, co na jutro","commit?","zamknąć laptopa"]
        }
        ];
    }

    getCheclists() {
        return this.checklists();
    }

    getChecklist(id) {
        var list = this.checklists();
        for (var i=0; i<list.length; i++) {
            if (list[i].id==id) return list[i];
        }
        return null;
    }

}