import React from 'react';
import {Component} from "react";
import Checkbox from '@material-ui/core/Checkbox';
import {Link} from "react-router-dom";


export class Checklist extends Component {

    componentDidMount() {
        document.title=this.props.name;
    }

    save() {

    }

    render() {
        var elems = [];
        var id = Math.random()+new Date()
        var items = this.props.items||[];
        // var text = encodeURIComponent(JSON.stringify(items));
        if (this.props.edit) {
            var t="";
            for (var i=0; i<items.length; i++) t+=items[i]+"\n";
            return <div><textarea rows={20} cols={50} value={t}></textarea><br /><button onClick={this.save}>Save</button></div>
        } else {
            for (var i = 0; i < items.length; i++) {
                var label = items[i];
                if (this.props.edit) {
                    label = <input type="text" value={label}></input>;
                } else {
                    label = <label for={id + "item_" + i}>{label}</label>
                }
                elems.push(<div><Checkbox id={id + "item_" + i} label={"item " + i}></Checkbox>{label}</div>)
            }
            return <div><h1>{this.props.name}</h1>{elems}<br /><Link to="/list">&lt;&lt; Back</Link></div>;
        }
    }
}